/* ———————————————————————————————————————————————————————————————————————————————
==============================—Style—normalization—===============================
——————————————————————————————————————————————————————————————————————————————— */

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,*:before,*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {outline: none;}
a:focus, a:active {outline: none;}

nav, footer, header, aside {display: block;}

@font-face {
  font-family: 'Rosario';
  src: url('./fonts/Rosario.ttf');
  font-weight: 500;
}

@font-face {
  font-family: 'Voga';
  src: url('./fonts/voga_medium.otf');
  font-weight: 500;
}

html, body {
  width: 100%;

  font-family: 'Rosario';
  font-size: 100%;
  line-height: 1;
  font-size: 15px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  background: linear-gradient(45deg, rgb(243, 255, 240), rgb(230, 255, 255), lightcyan) ;
}

input, button, textarea {font-family: inherit;}

input::-ms-clear {display: none;}
button {cursor: pointer;}
button::-moz-focus-inner {padding: 0; border: 0;}
a, a:visited {text-decoration: none;}
a:hover {text-decoration: none;}
ul li {list-style: none;}
img {vertical-align: top;}

h1, h2, h3, h4, h5, h6, p, span {font-family: inherit; font-size: inherit; }

/* ----------------------------------------------------------------------------- */

.cv-page-wrapper {
  width: 70%;
  min-height: 100%;
  margin: 0 auto;
  overflow: hidden;

  background-color: #fff;
  box-shadow: 0px 0px 10px rgb(130, 130, 130);
}

@media (max-width: 1024px) {
  .cv-page-wrapper {
    width: 90%;
  }
}

@media (max-width: 768px) {
  .cv-page-wrapper {
    width: 96%;
  }
}

header {
  background: linear-gradient(125deg,rgb(252, 252, 252), rgb(234, 255, 240),rgb(234, 255, 245), rgb(214, 244, 244));
  
}

main {
  background: linear-gradient(75deg,rgb(252, 252, 252), rgb(234, 255, 240),rgb(234, 255, 245), rgb(214, 244, 244));
}

.title {
  background: linear-gradient(45deg,  rgb(36, 50, 70)60%, rgb(36, 154, 112)10%, #5100a3);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #0B2349;
  display: table;
}

.title_about {
  background: linear-gradient(45deg,  white, lightcyan, lightskyblue );
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #0B2349;
  display: table;
}

.title, .title_about {
  font-family: 'Voga, sans-serif';
}

@media (max-width: 768px) {
  .title, .title_about {
    margin: 0 auto;
  }
}

.dotIcon {
  font-size: large;
  color: black;
}
